form label {
	display: flex;
	flex-direction: column;
	padding: 0.5rem;
}

form label span {
	font-size: 0.9rem;
}

textarea,
form label input:not([type]),
form label input[type="text"],
form label input[type="password"] {
	border-radius: 6px;
	border: 1px solid #999;
	padding: 0.65rem 0.75rem;
	font-size: 0.95rem;
	margin-top: 0.5rem;
	margin-bottom: 0.5rem;
}

form label input[type="submit"],
form label input[type="button"],
form label button {
	align-self: flex-end;
}

.edit-profile form {
	margin-top: 1rem;
}

.edit-profile form,
.edit-profile form label {
	display: flex;
	flex-direction: column;
}

.edit-profile form label {
	margin-bottom: 1rem;
}

textarea {
	resize: none;
	max-height: 320px;
}
